.payment{
    background-color: white;
}

.payment_container h1{
    text-align: center;
    background-color: rgb(234,237,237);
    border-bottom: 1px solid lightgray;
    padding: .5rem;
    /* font-weight: 400; */
}

.payment_container h1 a{
    color: black;
    text-decoration: none;
}

.payment_section{
    display: flex;
    border-bottom: 1px solid lightgray;
    padding: 1rem;
    margin: 0 1rem;
}
 .d{
    flex-direction: column;
 }

 .payment_title{
    flex: 0.2;
 }

 .payment_address, .payment_details{
    flex: 0.8;
 }